  margin-top: 20px;
}

div#parent-container {
  text-align: center;
 
}

div#preview {
  height: 40px;
  width: 40px;
  border-radius: 10px;
  display: inline-block;
  margin: 5px;
  margin-bottom: -10px;
}

button {
  margin: 0 auto;
  border: none;
  border-radius: 12px;
  color: whitesmoke;
  font-size: 17px;
  font-weight: bold;
  padding: 15px;
}

#rmv-row-btn{
  background-color: rgb(177, 0, 0);
  margin: 0 3px;
}

#rmv-row-btn:hover{
  background-color: white;
  transition: all 200ms ease-in;
  color: black;
  border: solid 1px black;  
}

